import React from "react"
import { withStyles } from "@material-ui/core/styles"
import Typography from "@material-ui/core/Typography"
import Button from "@material-ui/core/Button"
import Grid from "@material-ui/core/Grid"
import Paper from "@material-ui/core/Paper"
import style from "styles/components/project"

function Project({ title, description, image, path, classes }) {
    const onNavigate = (path) => {
        window.location = path
    }

    return (
        <Grid container justify="center" className={classes.root}>
            <Paper className={classes.paper}>
                <Grid container justify="space-between">
                    <Grid item xs={12} md={5}>
                        <Typography variant="h4"align="left">
                            {title}
                        </Typography>
                        <br/>
                        <Typography variant="body1">
                            {description}
                        </Typography>
                        {path && <Grid item xs={12} className={classes.buttonWrapper}>
                            <Button
                                className={classes.button}
                                onClick={() => onNavigate(path)}
                            >
                                Source code
                            </Button>
                        </Grid>}
                    </Grid>
                    <Grid item xs={12} md={6} className={classes.imageWrapper}>
                        <img className={classes.image} src={image} alt="Project" />
                    </Grid>
                </Grid>
            </Paper>
        </Grid>
    )
}

export default withStyles(style)(Project)
