import React from "react"
import { useHistory } from "react-router-dom"
import { withStyles } from "@material-ui/core/styles"
import AppBar from "@material-ui/core/AppBar"
import Toolbar from "@material-ui/core/Toolbar"
import Button from "@material-ui/core/Button"
import CssBaseline from "@material-ui/core/CssBaseline"
import Grid from "@material-ui/core/Grid"
import Typography from "@material-ui/core/Typography"
import LinkedInIcon from "@material-ui/icons/LinkedIn"
import style from "styles/components/layout"

function Layout({ children, classes }) {
    const onCreator = () => {
        window.location = "https://www.linkedin.com/in/erikstahlberg/"
    }

    const history = useHistory()
    const path = history.location.pathname

    return (
        <Grid container className={classes.root}>
            <CssBaseline />
            <AppBar position="sticky">
                <Toolbar className={classes.navbar}>
                    <Button
                        className={path === "/" ? classes.selected : ""}
                        onClick={() => history.replace("/")}
                    >
                        <Typography>Home</Typography>
                    </Button>
                    <Button
                        className={path === "/contact" ? classes.selected : ""}
                        onClick={() => history.replace("/contact")}
                    >
                        <Typography>Contact</Typography>
                    </Button>
                    <Button
                        className={path === "/about" ? classes.selected : ""}
                        onClick={() => history.replace("/about")}
                    >
                        <Typography>About me</Typography>
                    </Button>
                </Toolbar>
            </AppBar>
            <div className={classes.body}>
                {children}
            </div>
            <div className={classes.footer}>
                <Grid container justify="space-between">
                    <Grid item xs={12} md={6} className={classes.copyright}>
                        <Typography>Copyright © Joakim Ståhle-Nilsson 2020</Typography>
                    </Grid>
                    <Grid item xs={12} md={6} className={classes.creator}>
                        <Typography className={classes.creatorText}>
                            Website created by Erik Ståhlberg
                        </Typography>
                        <Button
                            className={classes.linkedInButton}
                            onClick={() => onCreator()}
                        >
                            Linked<LinkedInIcon />
                        </Button>
                    </Grid>
                </Grid>

            </div>

            {/* </Grid> */}
        </Grid>
    )
}

export default withStyles(style)(Layout)
